$regular: "CoFoSans-Regular";
$medium: "CoFoSans-Medium";
$bold: "CoFoSans-Bold";
$black: "CoFoSans-Black";

@font-face {
  font-family: $regular;
  src: url("../fonts/CoFoSans-Regular.woff2") format("woff2"), url("../fonts/CoFoSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: $medium;
  src: url("../fonts/CoFoSans-Medium.woff2") format("woff2"), url("../fonts/CoFoSans-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: $bold;
  src: url("../fonts/CoFoSans-Bold.woff2") format("woff2"), url("../fonts/CoFoSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: $black;
  src: url("../fonts/CoFoSans-Black.woff2") format("woff2"), url("../fonts/CoFoSans-Black.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

:root,html {
  font-size: 10px;
}

body {
  margin: 0;
  --color-text: #112200;
  --color-bg: #E5E5E5;
  --color-link: #112200;
  --color-link-hover: #414141;
  --color-menu: #112200;
  --color-menu-hover: #E7411F;
  color: var(--color-text);
  background-color: var(--color-bg);
  --cursor-stroke: #000;
  --cursor-fill: #000;
  --cursor-stroke-width: 1px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: $regular, sans-serif;
}

.js {
  .loading{
    &::before,
    &::after {
      content: "";
      position: fixed;
      z-index: 1000;
    }
    &::before {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: var(--color-bg);
    }
    &::after {
      top: 50%;
      left: 50%;
      width: 60px;
      height: 60px;
      margin: -30px 0 0 -30px;
      opacity: 0.4;
      background: var(--color-link);
      animation: loaderAnim 0.7s linear infinite alternate forwards;
    }
  }
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}

header {
  position: absolute;
  width: 100%;
  top: 0;
  display: grid;
  grid-template-columns: 81px 1fr 1fr auto;
  text-transform: uppercase;
  z-index: 1;

  .trigger-input{
    display: block;
    width: 81px;
    height: 81px;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    opacity: 0;
    z-index: 11;
    order: 3;
    -webkit-touch-callout: none;
    &:checked ~ #trigger {
      filter: grayscale(0.81);
    }

    &:checked ~ .menu {
      filter: opacity(0);
    }
  }
}

.logo{
  font: 3.4rem/0.9 $bold;
  color: var(--color-text);
  margin: 18px 0 auto 0;
  grid-column-start: 2;
}

.menu{
  display: flex;
  flex-direction: column;
  margin: 18px 0 auto 5%;
  order: 2;
  font:1.8rem $medium;
  transition: transform 0.3s ease-out;
  a{
    color: var(--color-link);
    margin-bottom: 3rem;
  }
}

#trigger {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 8.1rem;
  height: 8.1rem;
  border: none;
  background: var(--color-menu-hover);
  background-size: 100% 3px;
  transition: filter 0.3s ease-in-out;
  cursor: pointer;
  outline: none;
  z-index: 10;
  margin: 0 0 0 auto;
  order: 3;
  font: 1.6rem $bold;
  
  &:before{
    content: attr(title);
    color: white;
    text-transform: uppercase;
    display: block;
  }
}

a {
  text-decoration: none;
  color: var(--color-link);
  outline: none;
  cursor: pointer;
  &:hover,
  &:focus{
    color: var(--color-link-hover);
    outline: none;
  }
}

.items {
  width: 100%;
  padding: 0;
  padding: 0;
  margin: auto;
  position: relative;
  user-select: none;
  counter-reset: menucounter;
  display: grid;
  grid-template-columns: 7.5rem 1fr 1fr 0.9fr 0.9fr 1fr 1fr 7.5rem;
  grid-gap: 6.3rem;
  &.active{
    padding-top: 213px !important;
    .item.active{
      grid-column: 4/6;
      order: 2;
      margin: auto;
      height: auto;
      font-family: $regular;
      color: var(--color-text) !important;
      picture{
        &::before{
          display: none;
        }
      }
      img{
        transform: none !important;
      }
      .item-text{
        display: block;
      }
      .hover{
        display: block;
      }
    }
  }
  .item.active{
    .hover{
      display: none;
    }
  }
}

.item {
  position: relative;
  order: 2;
  &:hover {
    h4 {
      color: var(--color-menu-hover);
    }
    &::before {
      opacity: 1;
      transform: translateX(0);
    }
  }
  &::before {
    counter-increment: menucounter;
    content: counters(menucounter, ".", decimal-leading-zero);
    position: absolute;
    color: white;
    left: 1.2rem;
    top: 1.2rem;
    display: flex;
    align-items: center;
    line-height: 1;
    font-weight: bold;
    opacity: 0;
    transform: translateX(-1rem);
    transition: transform 0.3s, opacity 0.3s;
  }
  &:nth-child(1) {
    grid-column: 2/4;
    max-width: 48.5rem;
  }
  &:nth-child(2) {
    grid-column: 4/6;
  }
  &:nth-child(3) {
    grid-column: 6/8;
    max-width: 48.5rem;
  }
  &:nth-child(4) {
    grid-column: 3/6;
    max-width: 48rem;
  }
  &:nth-child(5) {
    grid-column: 6/8;
    max-width: 48rem;
  }
  
  &.active{
    grid-column: 1/9;
    order: 1;
    margin: 0;
    height: 100vh;
    position: relative;
    font:16.9rem/0.81 $medium;
    color: var(--color-text);
    &:before{
      display: none;
    }
    picture{
      height: 100%;
      overflow: hidden;
      &::before{
        content: attr(title);
        position: absolute;
        bottom: 116px;
        z-index: 1;
        max-width: 924px;
        margin: 0 auto;
        left: 15%;
        right: 0;
      }
    }
    .item-text{
      display: none;
    }
  }
  
  img {
    max-width: 100%;
  }
}

.item-text {
  position: relative;
  cursor: pointer;
  display: block;
  overflow: hidden;
  font-size: 2.6rem;
  margin-top: 20px;
}

h4 {
  display: block;
  font: 1.6rem/1.2 $bold;
  text-transform: uppercase;
  white-space: nowrap;
  color: var(--color-menu);
  margin-bottom: 12px;
}

p {
  font-size: 3.4rem;
  margin: 0;
}

.js h4 {
  transform: translateY(100%);
}

.hover {
  position: absolute;
  z-index: 0;
  width: 230px;
  height: 230px;
  border-radius: 50%;
  overflow: hidden;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;

  &-inner,
  &-img {
  width: 100%;
  height: 100%;
  position: relative;
}
  &-inner {
    overflow: hidden;
  }

  &-img {
    background-size: cover;
    background-position: 50% 50%;
  }
}

.cursor {
  display: none;
}

@media (any-pointer: fine) {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
    &__inner {
      fill: var(--cursor-fill);
      stroke: var(--cursor-stroke);
      stroke-width: var(--cursor-stroke-width);
      opacity: 0.9;
    }
  }
}

/*! locomotive-scroll v3.5.4 | MIT License | https://github.com/locomotivemtl/locomotive-scroll */
html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100vh;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: var(--color-text);
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

@media(max-width:1440px){
  p{
    font-size: 2.6rem;
  }
  h4{
    font-size: 1.2rem;
  }
  .item{
    &.active{
      font-size: 12.7rem;
    }
  }
}

@media(max-width:1170px){
  :root,html{
    font-size: 9px;
  }
  .items{
    grid-template-columns: 6rem 1fr 1fr 0.9fr 0.9fr 1fr 1fr 6rem;
    grid-gap: 5.4rem;
  }
  .item{
    &.active{
      height: auto;
      font-size: 11rem;
    }
  }
}

@media(max-width:980px){
  :root,html{
    font-size: 8px;
  }
  header{
    grid-template-columns: 2rem 1fr 1fr auto;
    grid-gap: 2rem;
  }
  .menu{
    font-size: 1.5rem;
  }
  .items{
    grid-template-columns: 2rem 1fr 1fr 0.9fr 0.9fr 1fr 1fr 2rem;
  }
  .item{
    &.active{
      font-size: 9rem;
    }
  }
}

@media(max-width:630px){
  .logo{
    font-size: 2.7rem;
  }
  .menu{
    a{
      margin-bottom: 1rem;
    }
  }
  .item{
    &.active{
      font-size: 5.4rem;
    }
  }
}

@media(max-width:500px){
  p{
    font-size: 1.8rem;
  }
  .logo{
    font-size: 2rem;
  }
  .menu{
    a{
      margin-bottom: 0.9rem;
    }
  }
  .items {
    grid-template-columns: 2rem 1fr 1fr 2rem;
    grid-gap: 4.5rem;
    &.active{
      .item.active{
        grid-column: 3;
      }
    }
  }
  .item{
    &:nth-child(2n-1) {
      grid-column: 2;
    }
    &:nth-child(2n) {
      grid-column: 3;
    }
    &.active{
      font-size: 3.5rem;
      grid-column: 1/5;
    }
  }
}

@media(max-width:440px){
  .items{
    grid-template-columns: 1rem 1fr 1fr 1rem;
    grid-gap: 3.5rem;
  }
}